}

.change-color__block {
  display: flex;
  position: relative;
}

.switch {
  width: 40px;
  height: 20px;
  flex: none;
  margin-left: 12px;
  margin-right: 12px;
  display: inline-block;
  position: relative;
}

.switch__input {
  width: 0;
  height: 0;
  opacity: 0;
}

.slider__round {
  cursor: pointer;
  border: .604701px solid #fd5103;
  border-radius: 20px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider__round:before {
  content: "";
  cursor: pointer;
  width: 16px;
  height: 16px;
  background-color: #fd5103;
  border-radius: 50%;
  position: absolute;
  top: 1px;
  bottom: 0;
  left: 1px;
  right: 0;
}

.switch__input:checked + .slider__round {
  background: #fd5103;
  border: .604701px solid #fff;
  border-radius: 20px;
}

.switch__input:checked + .slider__round:before {
  background-color: #fff;
  transform: translateX(20px);
}

.search-form {
  position: relative;
}

.svg-icon__search {
  fill: #fd5103;
  position: absolute;
  top: 13px;
  left: 8px;
}

