.cocktails-title {
  margin-top: 0;
  margin-bottom: 60px;

  text-align: center;
  font-weight: 600;
  font-size: 22px;
  line-height: 42px;
  @media screen and (min-width: 768px) {
    font-weight: 700;
    font-size: 32px;
  }
  @media screen and (min-width: 1280px) {
    font-weight: 700;
    font-size: 48px;
    line-height: 72px;
  }
}

.cocktails-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.list {
  list-style: none;
  padding: 0;
}

.cocktail-link {
  display: block;
  text-decoration: none;
}

.cocktail-card {
  display: block;
  margin-left: auto;
  margin-right: auto;

  width: 280px;
  max-height: 449px;

  background-color: inherit;
  text-align: center;

  border: 1px solid $accent;
  border-radius: 4px;
  @media screen and (min-width: 768px) {
    width: 335px;
    max-height: 541px;
  }
  @media screen and (min-width: 1280px) {
    width: 395px;
    max-height: 556px;
  }
}

.cocktail-picture {
  display: block;
  width: 100%;
  height: 280px;
  border-radius: 4px 4px 0px 0px;
  @media screen and (min-width: 768px) {
    height: 395px;
  }
  @media screen and (min-width: 1280px) {
    height: 395px;
  }
}

.cocktail-label {
  overflow: hidden;
  height: 42px;
  max-width: 238px;
  margin-top: 48px;
  margin-bottom: 16px;
  margin-left: auto;
  margin-right: auto;

  text-align: center;
  color: #202025;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

  font-weight: 900;
  font-size: 26px;
  line-height: 1.62;
  @media screen and (min-width: 768px) {
    height: 33px;

    margin-top: 20px;
    margin-bottom: 23px;

    font-size: 28px;
    line-height: 33px;
  }
  @media screen and (min-width: 1280px) {
    height: 38px;

    margin-top: 20px;
    margin-bottom: 27px;

    font-size: 32px;
    line-height: 38px;
  }
}

.cocktail-card-btn-wrapper {
  display: flex;
  justify-content: center;
  gap: 19px;
  margin-bottom: 24px;
  @media screen and (min-width: 768px) {
    margin-bottom: 22px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 28px;
  }
}

.learnMore {
  cursor: pointer;
  padding: 0;
  width: 108px;
  height: 39px;
  background: $accent;
  border: none;
  border-radius: 4px;

  letter-spacing: 0.02em;
  color: #ffffff;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  @media screen and (min-width: 768px) {
    width: 160px;
    height: 48px;

    font-size: 20px;
  }
  @media screen and (min-width: 1280px) {
    width: 182px;
    height: 48px;

    font-size: 20px;
  }
}

.addTo {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 9px;
  padding: 0;
  width: 110px;
  height: 39px;
  border: 1px solid $accent;
  border-radius: 4px;
  background-color: inherit;

  font-weight: 400;
  font-size: 14px;
  line-height: 1.71;
  letter-spacing: 0.02em;
  color: $accent;
  @media screen and (min-width: 768px) {
    width: 123px;
    height: 48px;

    font-size: 20px;
  }
  @media screen and (min-width: 1280px) {
    width: 141px;
    height: 48px;

    font-size: 20px;
  }
}
  .no-cocktails__wrapper {
    display: flex;
    gap: 60px;
    flex-direction: column;
    align-items: center;
  }

  .no-cocktails__text {
    width: 280px;
    text-align: center;
    font-family: Poppins;
    font-weight: 600;
    font-size: 22px;
    line-height: 1.91;

    @include tablet {
      width: 331px;
      font-family: Roboto;
      font-size: 32px;
      line-height: 1.31;
    }

    @include tabletPlus {
      font-weight: 700;
    }

    @include desktop {
      width: 493px;
      font-size: 48px;
      line-height: 1.5;
    }
  }

  .removeFrom {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    padding: 0;
    width: 110px;
    height: 39px;
    border: 1px solid $accent;
    border-radius: 4px;
    background-color: inherit;

    font-weight: 400;
    font-size: 14px;
    line-height: 1.71;
    letter-spacing: 0.02em;
    color: $accent;
    @media screen and (min-width: 768px) {
      width: 123px;
      height: 48px;

      font-size: 20px;
    }
    @media screen and (min-width: 1280px) {
      width: 141px;
      height: 48px;

      font-size: 20px;
    }
  }

  .icon-heart {
    width: 19px;
    height: 17px;
    --color1: #fd5103;
    @media screen and (min-width: 768px) {
      width: 21px;
      height: 19px;
    }
  }
  .icon-heart-selected {
    width: 15px;
    height: 15px;
    --color1: #fd5103;
    --color2: #fd5103;
    // transition: --color2 250ms cubic-bezier(0.4, 0, 0.2, 1);
    @media screen and (min-width: 768px) {
      width: 21px;
      height: 19px;
    }
  }
